.product-section {
    display: flex;
    column-gap: 50px;
    padding: 10px;
  
  }
  
  .product-gallery {
    display: flex;
    width: 500px;
    height: 500px;
    text-align: center;
    background: #fff;
    border-radius: 9px;
    border: 2px solid #D3D3D3;
    justify-content: center;
  }

  .subtitle2 {
    font-size: 30px;
    font-weight: 600;
    padding-bottom: 20px;
    
}

.order-block-tovar {
    display: flex;
    padding-left: 15px;
    justify-content: center;
    flex-wrap: wrap;
    height: 230px;
    width: 380px;
}



.buy-prod {
    padding: 20px;
    background-color: #F0F0F0;
    width: 320px;
    border-radius: 8px;
    height: 150px;
}

  .buy-prod .buy {
    width: 280px;
    height: 45px;
   
    border-radius:  8px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
   
    
}

  
  .aftertitle {
    
    
      width: 320px;
      height: 45px;
      display: flex;
      text-align: center;
      align-items: center;
      justify-content: center;
      background-color: #F0F0F0;
      border-radius: 8px;
     
  }
  
  .preinfo {
    display: flex;
    align-items: center;
    column-gap: 10px;
  
  }

  .main-image {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    max-height: 100%;
  }

  .img-main {
    padding: 30px;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    
  }
  
  .thumbnail-gallery {
    display: flex;
    
    height: 210px;
    flex-wrap: wrap;
    width: 150px;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
  }
  
  .thumbnail {
    display: flex;
    padding: 5px;
    object-fit: contain;
    height: 100px;
    width: 100px;
    border: 1px solid #ddd;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .thumbnail:hover {
    border-color: #007bff;
  }

  .product-specs {
   
  }
  
  .product-details  {
    display: flex;
    
  }
  .product-details h2 {
    
  }
  
  .product-title {
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 5px;
  }
  .product-title2 {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  

  .product-meta {
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
  }
  
  .product-rating {
  
    margin-bottom: 10px;
    font-size: 14px;
  }
  
  .product-rating .rating-text {
    color: #006F93;
  }
  
  .product-specs {
    list-style: none;
    padding: 0;
    margin-bottom: 20px;
  }
  
  .product-specs li {
    border-bottom: 1px solid #eee;
    padding: 8px 0;
  }
  
  .specs-link {
    display: inline-block;
    margin-top: 10px;
    color: #007bff;
    text-decoration: none;
  }
  
  .specs-link:hover {
    text-decoration: underline;
  }
  
  .product-price {
    margin-top: 20px;
  }
  

  
  .bonus {
    font-size: 14px;
    color: #666;
  }
  
  .add-to-cart {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .add-to-cart:hover {
    background-color: #0056b3;
  }
  
  /* Секция отзывов */
  .reviews-section {
    margin-top: 30px;
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .reviews-section h2 {
    margin-bottom: 20px;
    font-size: 22px;
  }
  
  .review-summary {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .review-rating {
    font-size: 36px;
    color: #007bff;
  }
  
  .review-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .review-item {
    border-bottom: 1px solid #eee;
    padding-bottom: 15px;
  }
  
  .review-author {
    font-weight: bold;
  }
  
  .review-text {
    margin: 10px 0;
  }
  
  .review-date {
    font-size: 14px;
    color: #666;
  }
  
  .review-stars {
    color: #ffc107;
    font-size: 16px;
  }