.items {
    display: flex;
    color: black;
    font-size: 22px;
    font-weight: 600;
    border-top: 2px solid #D9D9D9 ;
    width: 1000px;
    flex-wrap: wrap;

    min-height: 609px;
    

}



.cartItem {
    
    padding: 15px;
    display: flex;
    column-gap: 10px;
    position: relative;
    border-bottom: 2px solid #D9D9D9 ;
    width: 1000px;
    height: 190px;
}

.cartItemImg {
    align-items: center;
    text-align: center;
    width: 200px;
    height: 160px;
}

.main {
    display: flex;
    justify-content: center;
    
}

.main .number {
    display: flex;
   
}

.descrip .category {
    font-size: 18px;
    font-weight: 400;
    color: #868686;
    padding-bottom: 10px;
}

.descrip {
    width: 300px;
}

.number {
    width: 150px;
    display: flex;
    justify-content: center;
}

.price  {
    display: flex;
    width: 270px;
    justify-content: center;
    font-weight: 500;
}
.trashImg {
    display: flex;
    width: 50px;
    height: 30px;
    justify-content: center;
    cursor: pointer;
}

.precart {
    display: flex;
    padding: 12px;
    align-items: center;
    column-gap: 650px;
}

.precart span {
    font-weight: 600;
    font-size: 22px;
}
.precart a {
    font-weight: 500;
    font-size: 16px;
    color: #016a8a;
    text-decoration: underline;
    cursor: pointer;
}
.precart2 {
    display: flex;
    padding: 12px;
    align-items: center;
    border-bottom: 2px solid #D9D9D9;
}

.precart2 span {
    font-weight: 600;
    font-size: 22px;
    
}

.center {
    padding-top: 20px;
    text-align: center;
    line-height: 60px;
        
    
}

.precart2 a {
    font-weight: 500;
    font-size: 16px;
    color: #016a8a;
    text-decoration: underline;
    cursor: pointer;
}

.order-block {
    display: flex;
    padding-left: 15px;
    justify-content: center;
    flex-wrap: wrap;
    height: 300px;
    width: 380px;
}



.total-menu {
    padding: 20px;
    background-color: #F0F0F0;
    width: 320px;
    border-radius: 8px;
    height: 220px;
}

.cart-main {
    display: flex;
   flex-wrap: wrap;
  
}





.cartEmpty h1{
    color: #616161;
    font-weight: 600;
    font-size: 20px
}
.cartEmpty {
    min-height: 660px;
}
.cartEmpty p{
    color: #616161;
    font-size: 20px
}

.total-menu .buy {
    width: 280px;
    height: 45px;
    background-color: #0098C8;
    color: #E6E6E6;
    border-radius:  8px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
   
    
}


.main-total .subtitle {
    font-size: 25px;
    font-weight: 600;
    padding-bottom: 20px;
    
}

.basket-row-total {
    display: flex;
    justify-content: space-between;
    font-size: 18px;
}

.basket-row-final {
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
    padding-top: 20px;
}

.aftertitle {
    
    width: 320px;
    height: 45px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    background-color: #F0F0F0;
    border-radius: 8px;
   
}

.cart {
    margin: auto;
    height: auto;
    margin-bottom: 50px;
}

.counter {
    width: 60px;
    margin: 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border:  1px solid rgba(0, 0, 0, 0.226);
    
}

.number {
    height: 30px;
}